.gs-rule-field-container {
  display: flex;
}
.gs-rule-field-container div:first-child {
  flex: 2;
}
.gs-rule-field-container .ant-divider {
  height: auto;
  margin: 0 20px;
}
.gs-rule-field-container .gs-renderer {
  flex: 1;
}
.gs-rule-field-container .gs-renderer .gs-symbolizer-renderer {
  height: 100%;
  cursor: default;
}
.gs-rule-field-container .gs-renderer .gs-symbolizer-sldrenderer {
  height: 100%;
  cursor: default;
}
